div.page{

	display: flex;
	flex-direction: column;
	justify-content: center;

	width: 100%;
	height: 100%;
}

div.matchmaking{
	display: flex;
	flex-direction: column;
	justify-content: center;

	padding: 1em 1em;
	margin: auto;
	
	color: #f8f8f2;
	background-color: #43443b;
	border-radius: 1em;
	border: solid 0.1em #3e3d32;
	box-shadow: 0.5em 0.5em 0.3em #75715e;

	/*height: fit-content;*/
	/*width: fit-content;*/
	/*min-width: 70%;*/
	width: 60%;
	height: 60%;

	font-family: 'Courier New';
	font-size: 1.2em;
}

h2.matchmaking-text {
	text-align: center;
	color: #fd971f;
	margin: 0em;
}

span.gametitle{
	font-style: italic;
	color: #66d9ef;
}


a.matchmaking{
	font-size: 1em;
	color: #f8f8f2;

}

div.matchmaking-options{
	display: flex;
	flex-direction: row;
	color: #e6db74;
	width: 80%;
}

div.matchmaking-option{
	display: flex;
	flex-direction: column;
	justify-content: center;

	padding: 0.5em;
	width: 50%;
}

div.matchmaking-qrcode > svg{
	border-radius: 1em;
}

@media only screen and (max-device-width: 480px){

	div.matchmaking{
		font-size: 1em;
		width: 80%;
	}

	div.matchmaking-options{
		display: flex;
		flex-direction: column;
	}

	div.matchmaking-option{

		width: unset;
	}

}
